<template>
  <v-card height="auto">
    <v-card class="rounded-xl text-center" v-if="memberPositionList == ''" flat style="background-color: rgb(0, 40, 50)">
      <v-card-text>
        <div class="pt-16"></div>
        <div class="pt-16"></div>
        <span class="text-h5">No Data</span>
        <div class="pt-16"></div>
        <div class="pt-16"></div>
      </v-card-text>
    </v-card>

    <v-card v-if="memberPositionList != ''" class="rounded-xl" flat style="background-color: rgb(0, 40, 50)">
      <v-card-text>
        <v-card flat>
          <v-simple-table dark style="background-color: rgb(0, 40, 50)">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-center">Area</th>
                  <th class="text-center">Position</th>
                  <th class="text-center">Device</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(item, index) in memberPositionList"
                  :key="item.name"
                >
                  <td class="text-center">{{ displayArea(item.Area) }}</td>
                  <td class="text-center">{{ item.PositionId }}</td>
                  <td class="text-center">
                    <v-btn text color="primary" @click="openOwnCardModal(index)"
                      >View</v-btn
                    >
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- 會員礦機顯卡資訊彈窗 -->
    <v-row justify="center">
      <v-dialog v-model="memberOwnCardModal" persistent max-width="600">
        <v-card>
          <v-card-title>
            <span class="text-h5">My Miner</span>
          </v-card-title>
          <v-card-text>
            <v-simple-table fixed-header>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-center">Device</th>
                    <th class="text-center">Computing Power</th>
                    <th class="text-center">Electricity</th>
                    <th class="text-center">Output Value</th>
                    <th class="text-center">Price</th>
                    <th class="text-center">Status</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in memberOwnCardList" :key="item.name">
                    <td class="text-center">{{ item.Graphics_Name }}</td>
                    <td class="text-center">{{ item.Computing_Power }}</td>
                    <td class="text-center">{{ item.Electricity }}</td>
                    <td class="text-center">{{ item.Output_Value }}</td>
                    <td class="text-center">{{ item.Price }}</td>
                    <td class="text-center">
                      {{ displayCardStatus(item.IsUAllow) }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="blue darken-1"
              text
              @click="memberOwnCardModal = false"
            >
              Close
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-card>
</template>

<script>
import { memberCardList } from "../../api";

export default {
  data() {
    return {
      memberPositionList: "", //擁有位置資訊
      memberOwnCardModal: false,
      memberOwnCardList: "", //擁有設備列表
    };
  },
  created() {
    this.getMemberPositionList();
  },
  methods: {
    //會員礦機位置api
    getMemberPositionList() {
      memberCardList()
        .then((result) => {
          this.memberPositionList = result.data.list;
        })
        .catch((err) => {
          console.error(err);
        });
    },

    openOwnCardModal(index) {
      this.memberOwnCardModal = true;
      this.memberOwnCardList = this.memberPositionList[index].cardList;
    },

    displayArea(area) {
      if (area == 0) {
        return "A";
      } else if (area == 1) {
        return "B";
      } else if (area == 2) {
        return "C";
      }
    },

    displayCardStatus(status) {
      if (status == 1) {
        return "Normal";
      } else {
        return "Breakdown ";
      }
    },
  },
};
</script>